import OrderHeader from '../../../types/order/orderHeader';

const getStatusName = (orderStatus: number): string => {
  if (orderStatus === 2) {
    return '';
  }
  if (orderStatus === 3) {
    return '';
  }
  if (orderStatus === 4) {
    return '';
  }

  return '';
};

const crateStatusSummary = (order: OrderHeader): string => {
  const {
    issueName,
    deputeIssueName,
    issueDate,
    auditName,
    auditDate,
    orderStatus,
  } = order;

  const statusName = getStatusName(orderStatus);

  return `${issueName}
    ${deputeIssueName}
    ${issueDate}
    ${auditName}
    ${auditDate}
    ${statusName}
    `.replaceAll('null', '');
};

export default crateStatusSummary;
